<template>        <link href="https://cdn.jsdelivr.net/npm/bootstrap@5.3.3/dist/css/bootstrap.min.css" rel="stylesheet" integrity="sha384-QWTKZyjpPEjISv5WaRU9OFeRpok6YctnYmDr5pNlyT2bRjXh0JMhjY6hW+ALEwIH" crossorigin="anonymous">
    <div class="container card">
      <h2>Registro</h2>
      <div class="form-group m-2">
        <label class="col-2" for="_id">Id</label>
        <input class="col-4" type="text" id="_id" placeholder="Id" />
      </div>
      <div class="form-group m-2">
        <label class="col-2" for="name">Clave</label>
        <input class="col-4" type="text" id="name" placeholder="Nombre" />
      </div>
  
      <div class="m-2">
        <button @click="goToRegistro" class="">Entradas</button>
        <button @click="goToSalidas" class="">Salidas</button>
        <button @click="goToCancelar">Cancelar</button>
      </div>
    </div>
  </template>
  
  <script>
  export default {
    methods: {
        goToRegistro() {
      this.$router.push('/rEntrada'); 
    },
    goToSalidas() {
      this.$router.push('/salida'); 
    },
    goToCancelar() {
      this.$router.push('/'); 
    },

    }
  }
  </script>